.ffblock-components-popover,
.ffblock-panel-control-wrapper {
	.ffblock-control-field {
		&.ffblock-control-background-wrap {
			.ffblock-control-body-btn-wrap {
				display: flex;
				align-items: center;
				gap: 6px;
				margin-bottom: 14px;

				.ffblock-btn-group {
					flex-grow: 1;
				}

				.ffblock-reset-btn {
					padding: 0;
					background-color: transparent;

					&:hover {
						background-color: transparent;
					}

					&:focus,
					&:hover,
					&:active {
						outline: 0;
						box-shadow: none;
					}
				}
			}
		}
	}
}