.ffblock-components-popover,
.ffblock-panel-control-wrapper {
	.ffblock-device {
		&-switcher {
			color: gray;
			display: flex;
			align-items: center;
			justify-content: center;
			position: relative;
			z-index: 2;
			cursor: pointer;
			height: 0;
			transform: scale(0);
			opacity: 0;
			transition: 0.15s;
			font-size: 12px;

			i {
				font-size: initial;
			}

			&.active {
				height: 24px;
				transform: scale(1);
				opacity: 1;
				color: var(--ffblock-control-color-primary, #0150ec);
			}
		}

		&-switchers {
			position: relative;
			width: 24px;
			height: 24px;

			&-wrap {
				position: absolute;
				width: 100%;
				top: 1px;
				color: #e0e1e3;
				z-index: 44;
			}

			&-open {
				.ffblock-device-switchers-wrap {
					box-shadow: 0 0 20px 0 rgb(0 0 0 / 25%);
					background: #ffffff;
					border-radius: 2px;
				}

				&.active-md {
					.ffblock-device-switchers-wrap {
						top: 0;
					}
				}

				&.active-sm {
					.ffblock-device-switchers-wrap {
						top: 0;
					}
				}

				&.active-xs {
					.ffblock-device-switchers-wrap {
						top: 0;
					}
				}

				.ffblock-device-switcher {
					height: 24px;
					transform: scale(1);
					opacity: 1;
				}
			}
		}
	}
}