.ffblock-panel-control-wrapper {
	.ffblock-tab-group {
		display: flex;
		position: relative;
		background: #fff;
		//z-index: 9;

		//background: #fff;
		//border-top: 1px solid #d1d1d1;
		position: sticky;
		top: 47px;
		z-index: 999;
		box-shadow: 0 4px 5px 0 rgba(32, 33, 36, .09);

		.ffblock-tab {
			flex-direction: column;
			justify-content: center;
			align-items: center;
			height: 60px;
			gap: 5px;
			width: 100%;
			color: #1a1a1a;
			border-top: 1px solid #e0e0e0 !important;
			box-shadow: none !important;
			border-bottom: 1px solid #e0e0e0 !important;
			background: #f2f2f2;
			border-right: 1px solid #e0e0e0;

			&.active {
				background: #dde7fb;
				color: #0150ec;
				border-bottom: 2px solid #0150ec !important;

				svg {
					path {
						fill: #0150ec !important;
					}
				}
			}

			svg {
				width: 18px;
			}

			.ffblock-tab-label {
				margin: 0;
				padding: 0;
				font-size: 13px;
				font-weight: 600;
			}
		}
	}

	.components-button:focus:not(:disabled) {
		box-shadow: none !important;
		outline: none !important;
	}

	.components-button-group .components-button+.components-button {
		margin-left: 0 !important;
	}
}