.ffblock-components-popover,
.ffblock-panel-control-wrapper {
	.ffblock-control-field {
		&.ffblock-control-typography-wrap {
			display: flex;
			align-items: center;
			justify-content: space-between;

			.components-button {
				font-size: var(--ffblock-control-text-base, 14px);
				background-color: #e0e0e0;
				height: 28px !important;
				width: 28px !important;
				padding: 0;
				display: inline-flex;
				align-items: center;
				justify-content: center;

				>span {
					transition: unset;
				}

				&:hover {
					color: var(--ffblock-admin-color-white, #fff);
					background-color: var(--ffblock-control-color-primary, #0150ec);
				}
			}
		}
	}

	.ffblock-typography-content {
		min-width: 230px;
		padding: 5px 6px;
	}
}