/*ffblock-panel-control-wrapper,
 ffblock-components-popover*/
.ffblock-components-popover,
.ffblock-panel-control-wrapper {
	--ffblock-control-color-primary: #0150ec;
	--ffblock-control-color-secondary: #555555;
	--ffblock-control-color-body: #333333;
	--ffblock-control-color-border: #e9e9e9;
	--ffblock-control-panel-btn-bg: #f9f9f9;
	--ffblock-control-panel-btn-bg-hover: #efefef;

	--ffblock-control-text-xs: 13px;
	--ffblock-control-text-base: 13px;
	--ffblock-control-text-sm: 13px;
	--ffblock-control-text-md: 16px;
	--ffblock-control-transition: all 0.3s ease-in-out;
	--wp-admin-theme-color: #0150ec;
	--wp-admin-theme-color--rgb: 0, 102, 255;
	color: var(--ffblock-control-color-body);

	.components-base-control {
		&__field {
			margin-bottom: 0;
		}

		&.components-toggle-control {
			.components-base-control__field {
				display: flex;
				flex-direction: row;
				justify-content: flex-start;
			}
		}
	}

	.components-base-control {
		.components-select-control {
			.components-input-control__label {
				padding-bottom: 0 !important;
			}
		}
	}

	// components-base-control, ffblock-control-field
	.components-base-control,
	.ffblock-control-field {
		--wp-admin-theme-color: #0150ec;
		--wp-admin-theme-color--rgb: 0, 102, 255;
		--column-gap: 10px;
		--row-gap: 10px;
		display: grid;
		gap: var(--row-gap, 10px);

		margin-bottom: 10px;

		&:last-of-type {
			margin-bottom: 6px;
		}

		&.ffblock-control-react-select {
			--row-gap: 6px;

			input[type="text"]:focus {
				box-shadow: none;
			}

			>div {
				>div {
					border-color: var(--ffblock-control-color-border, #e9e9e9);
				}
			}

			// react select popover menu
			[id*="react-select-"] {
				z-index: 45;
			}
		}

		// between
		&.ffblock-control-range-wrap,
		&.ffblock-control-dimension {
			.ffblock-control-head {
				justify-content: space-between;
			}
		}

		//ffblock-control-head
		.ffblock-control-head {
			display: flex;
			align-items: center;
			gap: var(--column-gap, 10px);
		}

		// ffblock-control-body
		.ffblock-control-body {
			.components-base-control {
				margin-bottom: 0 !important;
			}
		}

		.rt-left-part {
			display: flex;
			align-items: center;
			gap: var(--column-gap, 10px);
		}

		// units
		.ffblock-units-choices {
			display: flex;
			gap: 5px;
			justify-content: flex-end;

			label {
				cursor: pointer;
				padding: 0;
				text-transform: uppercase;
				font-size: 10px;
				font-weight: 500;

				&:hover,
				&.active {
					color: var(--ffblock-control-color-primary, #0150ec);
				}
			}
		}

		// components-base-control__help
		.components-base-control__help {
			font-size: 12px;
			color: var(--ffblock-control-color-secondary, #000);
			margin-top: -5px;
		}

		// label
		.components-input-control__label,
		.components-toggle-control__label,
		.components-base-control__label,
		.ffblock-label {
			font-size: var(--ffblock-control-text-base, 13px);
			color: var(--ffblock-control-color-secondary, #000);
			font-weight: 500;
			line-height: 1;
		}

		.components-input-control__label,
		.components-base-control__label {
			padding-bottom: var(--row-gap, 8px) !important;
			margin-bottom: 0 !important;
			display: inline-block;
			font-size: var(--ffblock-control-text-base, 13px) !important;
			text-transform: capitalize !important;
		}

		// inputs +  text-area
		.components-input-control__input,
		.components-select-control__input,
		.components-textarea-control__input,
		.components-text-control__input {
			line-height: 1.5 !important;
			height: 36px;
			padding: 8px;
			color: var(--ffblock-control-color-secondary, #555555) !important;
			border-radius: 3px;
			border: 1px solid var(--ffblock-control-color-border, #e9e9e9) !important;
			transition: var(--ffblock-control-transition, all 0.3s ease);

			&:focus {
				outline: 0;
				box-shadow: none;
				border-color: var(--ffblock-control-color-primary,
						#0150ec) !important;
			}
		}

		.components-input-control__input,
		.components-select-control__input,
		.components-text-control__input {
			height: 36px !important;
		}

		.components-textarea-control__input {
			height: 80px;
		}

		// components-input-control__backdrop
		.components-input-control__backdrop {
			display: none;
		}

		// components-form-toggle
		.components-form-toggle {
			.components-form-toggle__track {
				border-color: #000;
			}

			.components-form-toggle__input {
				&:focus {
					box-shadow: none;
					outline: 0;
				}
			}

			.components-form-toggle__input:focus+.components-form-toggle__track {
				box-shadow: none;
				outline: 0;
			}

			&.is-checked {
				.components-form-toggle__track {
					border-color: var(--ffblock-control-color-primary, #0150ec);
					background-color: var(--ffblock-control-color-primary, #0150ec);
				}

				.components-form-toggle__thumb {
					background-color: #fff;
					border-color: #fff;
				}
			}
		}
	}

	.ffblock-control-single-field {
		margin-top: 10px;
	}

	.ffblock-reset-btn {

		&.components-button,
		&.has-icon {
			font-size: 16px;
			padding: 0;
			background-color: transparent;

			&:hover {
				background-color: transparent;
			}

			&:focus,
			&:hover,
			&:active {
				outline: 0;
				box-shadow: none;
			}

			span {
				font-size: inherit;
				height: auto;
			}
		}
	}
}

/*.ffblock-components-popover*/
.ffblock-components-popover {

	.components-base-control,
	.ffblock-control-field {

		.components-input-control__label,
		.components-base-control__label {
			margin-bottom: 6px !important;
		}

		.components-input-control__input,
		.components-select-control__input,
		.components-text-control__input {
			height: 34px !important;
		}
	}
}

/*ffblock-components-popover*/
.ffblock-components-popover {
	--row-gap: 8px;

	.components-input-control__backdrop {
		display: none;
	}

	.components-popover__content {
		&::-webkit-scrollbar {
			width: 8px;
			border-radius: 8px;
		}

		&::-webkit-scrollbar-track {
			background: linear-gradient(to bottom,
					#0150ec 0%,
					#488cf1 100%,
					#9dff00 0%,
					#f5440f 100%);
			border-radius: 8px;
		}

		&::-webkit-scrollbar-thumb {
			background: transparent;
			box-shadow: 0px 0px 0px 1000vh black;
			border-radius: 8px;
		}
	}
}

/*ffblock-tab-controls*/
.ffblock-tab-controls {

	// panelbody tab components-button
	.components-button {
		font-size: var(--ffblock-control-text-base, 13px);
		background-color: var(--ffblock-control-panel-btn-bg, #f9f9f9);

		.components-panel__arrow {
			color: inherit;
		}

		&:hover {
			background-color: var(--ffblock-control-panel-btn-bg-hover, #efefef);
		}
	}

	.components-panel__body-title {
		>.components-button {
			font-weight: 500;
		}
	}

	// panelbody in panelbody
	.components-panel__body {
		.components-panel__body {
			margin-bottom: 10px;

			>.components-panel__body-title>.components-button {
				padding: 12px 48px 12px 16px;
			}
		}
	}

	//ffblock-btn-group
	.ffblock-btn-group {
		display: flex;
		align-items: center;
		justify-content: space-between;

		.components-button {
			box-shadow: none;
			outline: 0;

			&:active,
			&:focus,
			&:hover {
				box-shadow: none;
				outline: 0;
			}

			&.is-secondary {

				&:active,
				&:focus,
				&:hover {
					box-shadow: none;
					outline: 0;
				}

				&:hover {
					color: var(--ffblock-control-color-primary, #0150ec);
				}
			}
		}

		.ffblock-button,
		.components-button {
			display: inline-flex;
			align-items: center;
			justify-content: center;
			flex-grow: 1;
			font-size: var(--ffblock-control-text-xs, 12px);
			color: var(--ffblock-control-color-secondary, #555);
			background-color: var(--ffblock-admin-color-white, #fff);
			border: 1px solid var(--ffblock-control-color-border, #e9e9e9);
			padding: 2px;
			height: 32px;
			cursor: pointer;
			transition: var(--ffblock-control-transition, all 0.3s ease-in-out);

			&:first-of-type {
				border-radius: 4px 0 0 4px;
			}

			&:last-of-type {
				border-radius: 0 4px 4px 0;
			}

			&:not(:last-of-type) {
				border-right: none;
			}

			.components-button+.components-button,
			.ffblock-button+.ffblock-button {
				margin: 0;
			}

			&.is-primary,
			&.active {
				border-color: var(--ffblock-control-color-primary, #0150ec);
				color: var(--ffblock-admin-color-white, #fff);
				background-color: var(--ffblock-control-color-primary, #0150ec);
			}
		}

		&.ffblock-btn-group-state {
			margin-bottom: 8px;

			.components-button {
				height: 34px;
				font-weight: 500;
				font-size: var(--ffblock-control-text-base, 13px);
			}
		}
	}

	.components-panel__body.is-opened {
		>.components-panel__body-title {
			margin-bottom: 10px;
		}
	}
}


//image control
.ffblock-panel-control-wrapper {
	.ffblock-image-avatar {
		position: relative;
		width: 100%;
		height: 200px;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center center;
		margin-bottom: 5px;
		background-color: var(--ffblock-control-panel-btn-bg-hover, #efefef);

		.ffblock-img-avt-action-group {
			position: absolute;
			right: 15px;
			top: 15px;
			z-index: 10;
			display: flex;
			align-items: center;
			gap: 5px;

			.components-button {
				border: none;
				padding: 5px !important;
				min-height: 30px;
				width: auto;
				line-height: 1;
				height: auto;
				font-size: 15px !important;
				color: var(--ffblock-control-color-primary, #0150ec);
				transition: 0.3s;
				border-radius: 4px !important;
				display: flex;
				align-items: center;
				justify-content: center;
				width: 34px;
				height: 34px;

				&:hover {
					color: var(--ffblock-admin-color-white, #fff);
					background-color: var(--ffblock-control-color-primary, #0150ec);
				}

				&:focus {
					color: var(--ffblock-admin-color-white, #fff);
					background-color: var(--ffblock-control-color-primary, #0150ec);
				}
			}
		}
	}

	.ffblock-img-remove-btn {
		height: 100px;
		width: 100%;
		margin-bottom: 5px;

		span {
			font-size: 30px;
			width: auto;
			height: auto;
			margin: 0;
		}
	}
}

/*panelBody common section advanced button*/
.block-editor-block-inspector {
	.components-base-control {
		margin-bottom: 10px;

		&:last-of-type {
			margin-bottom: 6px;
		}

		.components-textarea-control__input,
		.components-text-control__input {
			line-height: 1.5 !important;
			height: 36px;
			padding: 8px;
			color: var(--ffblock-control-color-secondary, #555);
			border-radius: 3px;
			border: 1px solid var(--ffblock-control-color-border, #e9e9e9);
			transition: var(--ffblock-control-transition, all 0.3s ease-in-out);

			&:focus {
				outline: 0;
				box-shadow: none;
				border-color: var(--ffblock-control-color-primary, #0150ec);
			}
		}

		.components-textarea-control__input {
			height: 80px;
		}

		.components-base-control__label,
		.components-toggle-control__label {
			font-size: var(--ffblock-control-text-base, 13px);
			color: var(--ffblock-control-color-secondary, #555);
			font-weight: 500;
		}

		.components-base-control__label {
			margin-bottom: var(--row-gap, 10px);
			display: inline-block;
			text-transform: capitalize;
		}
	}

	&__advanced {
		.components-button {
			font-size: var(--ffblock-control-text-base, 13px);
			background-color: var(--ffblock-control-panel-btn-bg, #f9f9f9);
			font-weight: 500;
			border-bottom: 1px solid #e0e0e0;

			.components-panel__arrow {
				color: inherit;
			}

			&:hover {
				background-color: var(--ffblock-control-panel-btn-bg-hover, #efefef);
			}

			&:focus {
				outline: 0;
				box-shadow: none;
			}
		}
	}

	&__advanced.is-opened {
		.components-button {
			border-bottom-color: transparent !important;
		}
	}

	.components-panel__body {
		border: none;
		border-top: 1px solid #efefef;
		margin: 0;
	}
}

//tooltip button
.ffblock-panel-control-wrapper {
	[data-tooltip] {
		&:before {
			content: attr(data-tooltip);
			position: absolute;
			pointer-events: none;
			opacity: 0;
			display: inline-flex;
			align-items: center;
			justify-content: center;
			transition: all 0.15s ease;
			padding: 6px 10px;
			color: #fff;
			background: #1a1a1a;
			border-radius: 4px;
			border-width: 0;
			white-space: nowrap;
			text-align: center;
			line-height: 1.2;
			font-size: 13px;
			box-shadow: none;
			right: -70px;
		}

		&:hover {
			&::before {
				margin: 0;
				opacity: 1;
				z-index: 1000;
			}
		}
	}
}