.ffblock-components-popover,
.ffblock-panel-control-wrapper {
	.ffblock-control-field {
		&.ffblock-control-range-wrap {
			--row-gap: 5px;

			.components-input-control__input {
				height: 30px !important;
			}

			// components-range-control
			.components-range-control {
				&__wrapper {
					color: var(--ffblock-admin-color-primary, #0150ec);
					min-height: 25px;
					height: 25px;
					flex: unset;

					.acf-bf-cf-b-afb-199wkr {
						background-color: var(--ffblock-admin-color-primary,
								#0150ec);
					}
				}

				&__wrapper+.components-base-control.components-input-control {
					width: 50%;
				}

			}

			.ffblock-control-body {
				display: flex;
				align-items: center;
				gap: 8px;

				.components-base-control,
				.components-base-control__field,
				.ffblock-control-field {
					//flex-grow: 1;
					margin-bottom: 0;
					width: 100%;
				}

				.ffblock-reset-btn {
					font-size: 16px;
					color: var(--ffblock-control-color-secondary, #555);
					background-color: transparent;

					span {
						font-size: inherit;
					}

					&:focus {
						outline: 0;
						box-shadow: none;
					}
				}
			}
		}
	}
}